import { useState, useEffect } from 'react';
// material
import { Container, Typography, Grid, Stack, Tab, Tabs, Box, Tooltip, IconButton } from '@mui/material';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import { Icon } from '@iconify/react';
import roundFilterList from '@iconify/icons-ic/round-filter-list';
import { capitalCase } from 'change-case';
import Page from '../../components/Page';
import { ListAllUsers, SearchUsers, CheckInButton } from '../../components/_gate/general-check';
// ----------------------------------------------------------------------

export default function PageTwo() {
  const { themeStretch } = useSettings();
  const [viewModal, setViewModal] = useState(false);
  useEffect(() => {
    console.log('viewModal: ', viewModal);
  });

  const handleModal = () => {
    setViewModal(!viewModal);
  };
  const handleClose = () => {
    setViewModal(false);
  };

  const [currentTab, setCurrentTab] = useState('All Users');

  const ACCOUNT_TABS = [
    {
      value: 'All Users',
      component: <ListAllUsers />
    },
    {
      value: 'Customer',
      component: <ListAllUsers />
    },
    {
      value: 'Vendor',
      component: <ListAllUsers />
    },
    {
      value: 'Staff',
      component: <ListAllUsers />
    }
  ];

  const handleChangeTab = (event, newValue) => {
    setCurrentTab(newValue);
  };

  return (
    <Page title="Tasks" sx={{ fontFamily: 'inter' }}>
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Stack direction={{ xs: 'column', sm: 'row' }} sx={{ mt: -3 }}>
          <Stack sx={{ flexGrow: 1 }}>
            <Typography variant="subtitle1" sx={{ fontSize: '32px', lineHeight: '48px', fontWeight: '900' }}>
              Checked In People
            </Typography>
          </Stack>
        </Stack>
        <Stack spacing={2}>
          <Stack direction="row" spacing={3}>
            <Stack direction={{ xs: 'column', sm: 'row' }} spacing={8} alignItems="center">
              <Stack>
                <Tabs
                  value={currentTab}
                  scrollButtons="auto"
                  variant="scrollable"
                  allowScrollButtonsMobile
                  onChange={handleChangeTab}
                >
                  {ACCOUNT_TABS.map((tab) => (
                    <Tab
                      disableRipple
                      key={tab.value}
                      label={capitalCase(tab.value)}
                      icon={tab.icon}
                      value={tab.value}
                    />
                  ))}
                </Tabs>
              </Stack>
              <Stack>
                <SearchUsers />
              </Stack>
              <Stack>
                <Tooltip title="Filter list">
                  <IconButton>
                    <Icon icon={roundFilterList} />
                  </IconButton>
                </Tooltip>
              </Stack>
            </Stack>
          </Stack>
          <Stack sx={{ flexGrow: 1 }}>
            {ACCOUNT_TABS.map((tab) => {
              const isMatched = tab.value === currentTab;
              return isMatched && <Box key={tab.value}>{tab.component}</Box>;
            })}
          </Stack>
        </Stack>
        <Stack direction="row" alignItems="center" justifyContent="center" spacing={10} sx={{ mt: 7, ml: 70 }}>
          <CheckInButton />
        </Stack>
      </Container>
    </Page>
  );
}
